import { Task, Duration, StoreModel } from '../model/model'
import Utils from '../utils/Utils';
import stores from '../stores';
import 'reflect-metadata'
import '../../node_modules/reflect-metadata/Reflect.js'
import { plainToClass } from "class-transformer";


const STORAGE_KEY = 'DoingList3'
const storage = {
    isDebug: false,
    fetch: function (): StoreModel {
        if (this.isDebug) {
            return this._getMockStoreModel()
        }

        let result = new StoreModel()

        let storageJson = localStorage.getItem(STORAGE_KEY)
        if (storageJson != null) {
            return plainToClass(StoreModel, JSON.parse(storageJson))
        }

        return result
    },

    save: function (storeModel: StoreModel) {
        localStorage.setItem(STORAGE_KEY, JSON.stringify(storeModel))
    },

    _getMockStoreModel(): StoreModel {
        let result = new StoreModel()

        for (let index = 0; index < 12; index++) {
            const newLocal = new Task();
        newLocal.init("我的第 " + index + " 个任务")
            result.addTask(newLocal)
        }

        return result
    },
}

const model = {
    storeModel: storage.fetch(),

    save() {
        storage.save(this.storeModel)
    },

    setModel(model: StoreModel) {
        this.storeModel = model
        this.save()
    },
};

export default model